<template>
	<div class="container">
		<div class="row"></div>
		<h1 class="section-title__text">Додати новий запис(чернетку)</h1>
			<form @submit.prevent="addPost(item)">
				<div class="row">
					<div class="col-12">
						<div class="form-group">
						    <label for="title">Назва запису</label>
						    <input type="text" v-model='item.title' class="form-control" id="title">
						</div>
					</div>
					<!-- /.col-12 col-lg-6 -->					
					<div class="col-12">
						<div class="form-group">
					    	<label for="pre">Прев'ю запису:</label>
					    	<PreviewImage @getImg='getSrc' />
					  	</div>
					</div>
					<!-- /.col-12 -->				
				</div>
				<div class="form-group">
			    	<label for="excerpt">Короткий опис:</label>
			    	<textarea name="excerpt" class="form-control" v-model='item.excerpt' id="excerpt"></textarea>
			  	</div>
			  	<div class="form-group">
			    	<label for="describe">Контент:</label>
					<vue-editor for='describe' v-model="item.content"></vue-editor>
			  	</div>
			  	
				<button class="admin-button" type="submit">Додати роботу</button>
			</form>
	  		<router-link
	  			tag='button'
	  			@click='addToStorage(item)'
		      	class="admin-button" 
		      	:to="{ name: 'live_preview', params: { title: $_friendlyUrl(item.title), item }}"
		      >
		      	Попередній перегляд
		      </router-link>
			<button class="admin-button" @click='addToDraft(item)'>Додати до чернеток</button>	
	</div>
</template>
<script>
	import { VueEditor } from 'vue2-editor';

	const PreviewImage = () => import('@/components/PreviewImage');
	
	export default {
		name: 'NewPost',
		data() {
			return {
				item: JSON.parse(localStorage.getItem('item')) || {
					title: '',
					img: '',
					excerpt: '',
					content: ''
				},		
			}
		},
		components: {
		      VueEditor,
		      PreviewImage
		   },
		methods: {
		   	addPost (item) {      // <-- новый метод
		     	const createdAt = new Date();
		     	this.$_db.collection('blog').add({ ...item, createdAt })
		     	localStorage.removeItem('item');
		   	},
		   	addToStorage(item) {
		   		localStorage.setItem('item', JSON.stringify(item))
		   	},
		   	getSrc (data) {
		        this.item.img = data;
		    },
		    addToDraft(item) {      // <-- новый метод
		     	const createdAt = new Date();
		     	this.$_db.collection('draft_post').add({ ...item, createdAt });
		     	localStorage.removeItem('item');
		   	},
		 }
	}
</script>
<style scoped>
	input, select, textarea {
		display: block;
	}
</style>